.block_title_wr{
  display: flex;
  justify-content: space-between;
  margin: 30px 30px 0 30px;
  background-color: #ffffff;
}
.block_title_name{
  background-color: #ffffff;
  font-weight: 700;
  font-size: 16px;
}
.block_title_info{
  background-color: #ffffff;
  font-weight: 200;
font-size: 12px;
line-height: 14px;
}
.block_elipse_big *,.block_elipse_small * {
  background-color: #ffffff;
}
.block_elipse_small div svg {
  border-radius: 50%;
}
.block_elipse_wr{
  position: relative;
}
.block_elipse_small{
  
  position: absolute;
  top: 20px;
  left: 40px;
}
